// Typografy -------
html {
  font-size: $font-size; }
body {
  font-family: $font-family;
  &.lock {
    overflow: hidden; } }
// Typografy end ---

.swiper {
  overflow: hidden; }

// main position with header-page-footer
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
.page {
  flex: 1 1 auto;
 }  //padding: 75px 0 0 0
//end-

// Spoiler
.spoiler {
  display: inline-flex;
  align-items: center;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #FFCA41; }

  label {
    cursor: pointer;
    font-family: Bloggersans, serif;
    font-size: rem(18px);
    font-weight: 700;
    color: #FFCA41;
    margin: 0 8px 0 0; }
  &__rest {
    display: none; } }

//

// MODAL WINDOWS
.modal-info {
  font-family: Roboto, serif;
  font-weight: 400;
  line-height: em(24,15);
  max-height: 90vh;
  overflow: scroll;
  background: white;
  padding: 20px 20px;
  max-width: 770px;
  margin: 40px auto;
  position: relative;
  @include breakpoint(sm) {
    padding: 20px 30px; }
  @include breakpoint(md) {
    padding: 50px; }
  &_form {
    max-height: none;
    max-width: 590px;
    overflow: auto; }
  &__body {
    p {
      margin: 0 0 em(15,15) 0; } }
  &__title {
    text-transform: uppercase;
    font-weight: 700;
    color: #3983C8;
    margin: em(20,15) 0 em(15,15) 0; }
  &__list {
    li {
      padding: 0 0 0 10px;
      position: relative;
      &:before {
        content: '-';
        margin: 0 5px 0 0; } } }
  &__phone {
    display: block;
    color: #65C338; }

  &__form {}
  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 -15px;
    @include breakpoint(500) {
      justify-content: flex-start; } }
  &__column {
    flex: 0 0 50%;
    justify-content: center;
    padding: 0 15px; }

  &__input {}
  &__params {}
  &__btn {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 50px 0 0 0;
    .btn {
      padding: 0;
      margin: 0 20px 0 0;
      background: none;
      border: none;
      color: #65C338;
      @include breakpoint(450) {
        background-color: #FFFFFF;
        color: #65C338;
        border: 2px solid #65C338;
        padding: 0 37px;
        &:hover {
          background-color: #65C338;
          color: #FFFFFF;
          border: 2px solid #65C338; } }
      &:last-child {
        margin: 0;
        background-color: #FFFFFF;
        color: #65C338;
        border-radius: 26px;
        @include breakpoint(450) {
          background-color: #65C338;
          color: #FFFFFF;
          border: 2px solid #65C338;
          &:hover {
            background-color: #FFFFFF;
            color: #65C338;
            border-color: #65C338; } } } } }

  &_center {
    .modal-info {
      &__title {
        margin: 0 0 em(30,15) 0; }
      &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        .form__input {
          margin: 0; } }
      &__btn {
        margin: 30px 0 0 0;
        .btn {
          height: 40px;
          padding: 0;
          @include breakpoint(450) {
            padding: 0 25px; } } } } } }


