.about {
  &__body {
    background: linear-gradient(0deg, #3983C8, #3983C8);
    position: relative;
    padding: rem(60px) 0 rem(60px) 0;
    @include breakpoint(880) {
      padding: rem(100px) 0 rem(100px) 0; } }
  &__bg {
    position: absolute;
    background: url("../img/about-decor.svg") top center repeat-y;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  &__container {
    position: relative;
    z-index: 2;
    overflow: hidden;
    @include breakpoint(md) {
      padding: 0 107.5px; } }
  &__title {
    margin: 0 0 rem(55px) 0;
    @include breakpoint(md) {
      margin: 0 0 rem(85px) 0; }
    &:after {
      background-color: #FFCA41; } }
  &__content {
    font-family: Roboto, serif;
    color: #FFFFFF; }
  &__picture {}
  &__quote {
    display: inline-flex;
    align-items: flex-end;
    font-size: rem(22px);
    line-height: em(24,22);
    font-weight: 500;
    margin: rem(30px) 0 rem(15px) 0;
    .svg-quote {
      margin: 0 20px 0 0; }
    @include breakpoint(1070) {
      margin: 0 0 rem(15px) 0; } }
  &__text {
    font-weight: 300;
    font-size: rem(15px);
    line-height: em(24,15);
    p {
      margin: 0 0 em(15,15) 0; } } }


.picture-about {
  width: 100%;
  max-width: 370px;
  margin: 0 auto;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #5196D7;
    transform: rotate(60deg);
    z-index: -1; }
  @include breakpoint(1070) {
    float: right;
    max-width: 350px;
    max-height: 350px;
    margin: 0 0 rem(100px) rem(100px); }
  img {
    width: 100%; }
  &__info-block {
    text-align: center;
    font-family: Bloggersans, serif;
    color: #3983C8;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #FFFFFF;
    opacity: 0.9;
    padding: 20px; }
  &__title {
    font-size: rem(18px);
    font-weight: 700;
    margin: 0 0 em(6,18) 0; }
  &__subtitle {
    font-size: rem(15px); } }


