.form {
  font-family: Roboto, serif;
  &__input {
    display: flex;
    flex-direction: column;
    font-size: rem(12px);
    color: #707070;
    margin: 0 0 10px 0;
    label {
      margin: 0 0 0 17px; } }

  p {
    display: flex;
    align-items: center;
    position: relative;
    .check-icon {
      width: 20px;
      position: absolute;
      top: 1px;
      left: 3px;
      z-index: -1;
      > path {
        fill: none;
        stroke: #3983C8;
        stroke-width: 3;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-dasharray: 21px;
        stroke-dashoffset: 21px;
        transition: all 0.2s ease-out; } } }


  input[type='text'], input[type='tel'] {
    height: 34px;
    width: 200px;
    border-radius: 17px;
    border: 1px solid rgba(57, 131, 200, 0.7);
    padding: 10px 20px;
    &::placeholder {
      color: #E2E2E2; }
    &:focus {
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); }
    &.error {
      border: 1px solid rgba(233, 23, 23, 0.85); }
    &.access {} }


  input[type='radio'] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    + label {
      display: inline-flex;
      align-items: center;
      cursor: pointer;
      position: relative;
      &:before {
        content: '';
        display: inline-block;
        width: 20px;
        height: 20px;
        flex-shrink: 0;
        flex-grow: 0;
        border: 2px solid #3983C8;
        border-radius: 50%;
        margin-right: 10px; }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        width: 10px;
        height: 10px;
        background-color: #3983C8;
        border-radius: 50%;
        opacity: 0;
        visibility: hidden;
        transition: all 0.2s ease-out; } }
    &:checked {
      + label {
        font-weight: 700;
        &:after {
          opacity: 1;
          visibility: visible; } } } }


  input[type='checkbox'] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    + label {
      display: inline-flex;
      align-items: center;
      user-select: none;
      position: relative;
      cursor: pointer;
      &:before {
        content: '';
        display: inline-block;
        width: 20px;
        height: 20px;
        flex-shrink: 0;
        flex-grow: 0;
        border: 2px solid #3983C8;
        border-radius: 50%;
        margin-right: 10px; } }
    &:checked {
      + label {
        font-weight: 700;
        + .check-icon {
          > path {
            stroke-dashoffset: 0!important; } } } } } }







